@import "general";

/*------------------------------------------------- Start Home Page ------------------------------------------------*/
/*------------------------------- 
         [02.Start Header Area] 
---------------------------------*/
header {
    position:-webkit-sticky;
    position:sticky;
    top:0;
    background-color: #FFF;
    z-index: 9999999;
    box-shadow: .5px .5px 20px rgba($main-color, .3);
  & .navbar-brand {
    width: 320px !important;
    margin-right: 0 !important;
  }
}
header .custom-nav{
    width: 100%;
    z-index: 9999;
    border-bottom: 2px solid $color-g1;
    padding:0;
    
}

header .navbar-toggler span i{
    font-size: 25px;
    color: #FFF;
    -webkit-transition: all .54s ease-in-out;
       -moz-transition: all .54s ease-in-out;
         -o-transition: all .54s ease-in-out;
            transition: all .54s ease-in-out;
}

header .navbar-brand .img-fluid{
    width: 45% !important;
}

header .navbar-nav .nav-item{
    line-height: 50px;
    position: relative;
    -webkit-transition: all .54s ease-in-out;
       -moz-transition: all .54s ease-in-out;
         -o-transition: all .54s ease-in-out;
            transition: all .54s ease-in-out;

    .nav-icon{
        position: absolute;
        top:10px;
        left:-2px;
    } 

    & .nav-link:hover{color: $black-color;}

    & [class*="icon-basic-"]{
        -webkit-transition: all .2s ease-in-out;
           -moz-transition: all .2s ease-in-out;
             -o-transition: all .2s ease-in-out;
                transition: all .2s ease-in-out;
    }

    &:hover [class*="icon-basic-"]{color: $color-g1 !important;}

    .nav-link{
        position: relative;
        padding-right: 1.3rem !important;
        padding-left: 1.3rem !important;
        font-size: 1rem;
        color: $nav-grey;
        cursor: pointer;
        -webkit-transition: all .2s ease-in-out;
           -moz-transition: all .2s ease-in-out;
             -o-transition: all .2s ease-in-out;
                transition: all .2s ease-in-out;
        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            height: 2px;
            background-color: $main-color;
            width: 0;
            transition: all .54s ease
        }

        &:hover::after{width:100%;}
    }
}

header .navbar-nav .submenu-button{
    line-height: 50px;
    position: relative;
    padding-left: 1.5rem !important;
    padding-right: 1.5rem !important;
    cursor: pointer;
    -webkit-transition: all .2s ease-in-out;
       -moz-transition: all .2s ease-in-out;
         -o-transition: all .2s ease-in-out;
            transition: all .2s ease-in-out;
  color: #696969;

    .nav-icon{
        position: absolute;
        top:10px;
        left:-2px;
    }
    
    &:hover{color: $black-color !important;}

    & [class*="icon-basic-"]{
        -webkit-transition: all .2s ease-in-out;
           -moz-transition: all .2s ease-in-out;
             -o-transition: all .2s ease-in-out;
                transition: all .2s ease-in-out;
    }

    &:hover [class*="icon-basic-"]{color: $color-g1 !important}
}

header .navbar-nav .has-sub{
    & > span:after{
        content: "";
        position: absolute;
        display: block;
        top: 31px;
        right: 10px;
        width: 6px;
        height: 6px;
        -webkit-transform: rotate(45deg);
           -moz-transform: rotate(45deg);
             -o-transform: rotate(45deg);
                transform: rotate(45deg);
        border-style: solid;
        border-width: 0 2px 2px 0;
        border-color: transparent $nav-grey $nav-grey transparent
    }

    .menu{
        position: absolute;
        top: 68px;
        left: 0;
        padding: 0;
        background-color: $main-color;
        width: 200px;
        opacity: 0;
        visibility: hidden;
        -webkit-transform: translateY(30px);
           -moz-transform: translateY(30px);
             -o-transform: translateY(30px);
                transform: translateY(30px);
        -webkit-transition: all .3s ease-in-out;
           -moz-transition: all .3s ease-in-out;
             -o-transition: all .3s ease-in-out;
                transition: all .3s ease-in-out;
        
        &::after{
            content: "";
            position: absolute;
            top: -3px;
            left: 0;
            height: 2px;
            width: 0;
            background-color: $main-color;
            -webkit-transition: all .54s ease-in-out;
            -moz-transition: all .54s ease-in-out;
                -o-transition: all .54s ease-in-out;
                    transition: all .54s ease-in-out;
            -webkit-transition-delay: .2s;
            -moz-transition-delay: .2s;
                -o-transition-delay: .2s;
                    transition-delay: .2s;
        }

        & > li{
            display: block;
            position: relative;
            z-index: 9;
            -webkit-transition: all .2s ease-in-out;
               -moz-transition: all .2s ease-in-out;
                 -o-transition: all .2s ease-in-out;
                    transition: all .2s ease-in-out;
            cursor: pointer;
        }
         
        & > li > span::after{
            content: "\f0fe";
            font-family: 'Font Awesome\ 5 Free';
            font-weight: 900;
            display: inline-block;
            position: absolute;
            top: 11px;
            right: 10px;
        }

        & > li > .open::after{
            content: "\f146";
            font-family: 'Font Awesome\ 5 Free';
            font-weight: 900;
            display: inline-block;
            position: absolute;
            top: 11px;
            right: 10px;
        }

        & > li > span{
            color: #FFF;
            font-size: 15px;
            display: block;
            padding: 10px;
        }

        & > li ul{
            width: 100%;
            background-color: $color-g2;
            text-align: center;
            display: none;
            -webkit-transition: all .54s ease-in-out;
               -moz-transition: all .54s ease-in-out;
                 -o-transition: all .54s ease-in-out;
                    transition: all .54s ease-in-out;
        }

        & > li > ul > li > a{
            color: #FFF;
            font-size: 15px;
            display: block;
            padding: 10px 
        }
    }

    &:hover .menu::after{width: 100%;}

    .submenu-button{display: block;}

    &:hover .menu{
        opacity: 1;
        visibility: visible;
        -webkit-transform: translateY(0);
           -moz-transform: translateY(0);
             -o-transform: translateY(0);
                transform: translateY(0);  
    }
}

/* Media Query */
@media (max-width: 991px) {
    
    header {
        position: relative;
    }

    header .navbar-toggler span i{
        color: $main-color;
    }

    header .navbar-nav .has-sub .menu{
        opacity: 1;
        visibility: visible;
        -webkit-transform: translateY(0);
           -moz-transform: translateY(0);
             -o-transform: translateY(0);
                transform: translateY(0);
        position: relative;
        border-radius: 0;
        width: 100%;
        top: 0;
        display: none
    } 

    header .navbar-nav .nav-item,
    header .navbar-nav .submenu-button{
        line-height: 20px;
        -webkit-transition: all .64s ease-in-out;
           -moz-transition: all .64s ease-in-out;
             -o-transition: all .64s ease-in-out;
                transition: all .64s ease-in-out;
    }
    header .navbar-nav .has-sub{
        -webkit-transition: all .54s ease-in-out;
           -moz-transition: all .54s ease-in-out;
             -o-transition: all .54s ease-in-out;
                transition: all .54s ease-in-out;
        cursor: pointer;
    }

    header .navbar-nav .nav-item:hover,
    header .navbar-nav .has-sub:hover{
        padding-left: 5px;
    }

    header .navbar-nav .has-sub > span:after{
        top: 15px;
    }

}

