         [02.End Header Area] 
---------------------------------*/

/*------------------------------- 
         [03. Start Hero Area] 
---------------------------------*/
.hero-area{
  position: relative;
  .item {
    .single-item {
      .bg-image {
        height: 120vh;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;

