/*------------------------------- 
         [04.Start Event Area] 
---------------------------------*/
.event-area{
    .d-show-more{
        display: none
    }
    .show-more{
        text-align: center;
        .show-event{
            display: inline-block;
            padding: 8px 15px;
            background: linear-gradient(-45deg,#DDD,$color-g1 0%, $color-g2 100%,#DDD);
            color: #FFF;
            font-size: 18px;
            text-transform: capitalize;
             border-radius: 5px;
          -webkit-transition: all .2s ease-in-out;
          -moz-transition: all .2s ease-in-out;
          -o-transition: all .2s ease-in-out;
          transition: all .2s ease-in-out;
          &:hover{
            background: #FFF;
            color: $color-g1
          }
        }
    }
   .single-event{
       box-shadow: .5px .5px 20px rgba($main-color, .3);
       .event-img{
           border-top-left-radius: 5px;
           border-top-right-radius: 5px;
           overflow: hidden;
       }
       .event-name{
           margin: 10px;
           text-transform: capitalize;
         h3 {
           font-size: 18px;
           text-overflow:ellipsis;
           overflow: hidden;

         }
       }
       .event-content{
           color: $nav-grey;
           font-size: 15px;
       }
       .event-VM{
           display: block;
           text-align: right;
           padding: 10px 5px;
           .MV-link{
                display: inline-block;
                padding: 5px 10px;
                background: linear-gradient(-45deg,#DDD,$color-g1 0%, $color-g2 100%,#DDD);
                color: #FFF;
                border-radius: 3px;
                -webkit-transition: all .2s ease-in-out;
                -moz-transition: all .2s ease-in-out;
                -o-transition: all .2s ease-in-out;
                transition: all .2s ease-in-out;
                cursor: pointer;
             &:hover {
               background: #FFF;
               color: $color-g1
             }
           }
       }
   }
} 


