---------------------------------*/
.scrollToTop{
    position: fixed;
    bottom: 20px;
    right: 20px;
    color: #FFF;
    font-size: 25px;
    cursor: pointer;
    display: none;
    background-color: #28a745;
    border-radius: 50%;
    border: 2px solid #28a745;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    -webkit-transition: all .54s ease-in-out;
       -moz-transition: all .54s ease-in-out;
         -o-transition: all .54s ease-in-out;
            transition: all .54s ease-in-out;
    &:hover{
        transform: translateY(-5px);
    }
}
